// import { describe, it, expect } from 'vitest';
import { ensureTrailingSlash, assertNoApiPrefix } from './http-path';

describe('http path helpers', () => {
  it('adds trailing slash', () => {
    expect(ensureTrailingSlash('/users/me')).toBe('/users/me/');
    expect(ensureTrailingSlash('/users/me/')).toBe('/users/me/');
  });
  it('rejects /api prefix', () => {
    expect(() => assertNoApiPrefix('/api/users/me/')).toThrow();
    expect(assertNoApiPrefix('/users/me/')).toBe('/users/me/');
  });
});


